@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@300;400;500;600&display=swap');

body {
  background: linear-gradient(135deg, rgb(0, 0, 0) 0%, rgb(64, 20, 103) 100%);
  animation: gradient 10s ease infinite;
  background-size: 200%;
  font-family: 'Barlow', sans-serif;
  font-weight: 500;
  margin-block: 5dvh;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

h1 {
  font-size: 3.5em;
  font-weight: 800;
  /* font-style: italic; */
  font-family: 'Barlow', sans-serif;
}

h2 {
  font-size: 2.5em;
  font-weight: 600 !important;
  font-family: 'Barlow', sans-serif;
}

h1, h2, h3 {
  /* padding: 2vmin; */
  margin: 0 !important;
}

p {
  font-size: 1.1em;
  padding-block: 2vmin;
  margin: 0 !important;
}

.col {
  padding: 0 !important;
}

ul {
  font-size: larger;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

a {
  color: black !important;
  font-weight: 600;
}

.card, .card-action {
  border-radius: 7px;
  padding: 5px;
  margin: 5px;
  height: clamp(200px, 45%, 350px);
}

.demoPic {
  width: 200px;
  height: 200px;
  object-fit: cover;
  object-position: center top;
  border-radius: 7px !important;
}

#portrait {
  margin: 15px;
  border-radius: 15px;
  object-fit: fill;
  box-shadow: 2px 2px 14px 1px rgba(0, 0, 0, 0.8);
  justify-self: center;
  aspect-ratio: initial;
  height: 300px;
  width: auto;
}

@media ( width <= 600px) {
  .main {
    width: 100vw;
  }
  .demoPic {
    width: 100px;
    align-self: center;
  }
  .horizontal {
    flex-direction: column;
    align-items: center;
  }
  .card-image {
    max-width: auto !important;
  }
}